.features {
  background-color: $black-pearl;
  width: 100%;
  display: flex;
  justify-content: center;
  // max-width: 1320px;
  flex: 1;
}

.features__container {
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 70px;
  padding-bottom: 140px;
  // width: $vp-1920;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: $vp-767) {
    padding: 0 25px;
    padding-top: 50px;
    padding-bottom: 86px;
  }
  // @media (max-width: $vp-1920) {
  //     padding-left: 50px;
  //     padding-right: 50px;
  //     padding-bottom: 75px;
  //     padding-top: 60px;
  //    }
}

.features__title {
  font-family: $roboto;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin-bottom: 55px;
  margin-top: 0;

  @media (max-width: $vp-767) {
    display: none;
  }
}

.features__title---mobile {
  display: none;

  @media (max-width: $vp-767) {
    font-family: $roboto;
    font-weight: 400;
    display: block;
    font-size: 28px;
    line-height: 39px;
    // line-height: 51px;
    color: $color-default-white;
    margin-top: 0;
    margin-bottom: 40px;
  }
}

.features__list {
  list-style: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 85px;
  margin: 0;

  @media (max-width: $vp-1023) {
    grid-template-columns: 1fr;
    gap: 30px;
    width: 100%;
  }
  // @media (max-width: $vp-770) {
  // }
}

.features__link {
  position: relative;
  display: block;
}

.features__link:hover {
  opacity: 0.8;
}

.features__link:focus {
  opacity: 0.8;
}

.features__link:active {
  opacity: 0.6;
}

.features__link::after {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../../img/svg/arrow.svg");
  background-color: $deep-sky-blue;
  color: $color-default-white;
  background-position: center;
  background-repeat: no-repeat;
  bottom: 0;
  right: 0;

  @media (max-width: $vp-767) {
    width: 40px;
    height: 40px;
  }
}

.features__link img {
  display: block;

  @media (max-width: $vp-1023) {
    object-fit: cover;
    width: 100%;
  }
  //    @media (max-width: $vp-767) {
  //     width: 670px;
  //     height: 250px;
  //     object-fit: cover;
  //    }
}

.features__link span {
  font-family: $roboto;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;
  position: absolute;
  width: 45%;
  top: 7%;
  left: 12%;

  @media (max-width: $vp-1023) {
    width: 24%;
  }

  @media (max-width: $vp-767) {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    width: 60%;
  }
}

