.advantages {
    background-color: $color-default-white;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    width: 100%;
}

.advantages__container {
    max-width: 1320px;
    flex: 1;
}

.advantages__wrapper {
    // display: grid;
    // grid-template-columns: 150px 1fr;
    padding: 0 50px;
    // gap: 100px;
    padding-top: 120px;
    padding-bottom: 100px;

    @media (max-width: $vp-1023) {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    @media (max-width: $vp-767) {
        padding: 0 25px;
        padding-top: 50px;
        padding-bottom: 80px;
        //   display: block;
    }
}

.advantages__list {
    list-style: none;
    padding-left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;

    @media (max-width: $vp-767) {
       grid-template-columns: 1fr;
    }
}

.advantages__item {
    min-height: 350px;
    border: 1px solid $whisper-dark;
    border-left: none;
    // 5px solid $deep-sky-blue; 
}

.advantages__item-wrapper {
    padding: 95px 45px 10px 45px;
    height: 100%;
    border-left: 5px solid $deep-sky-blue; 
}

.advantages__item:hover {
    background-color: $deep-sky-blue;
}

.advantages__item:hover .advantages__item-wrapper p {
    color: $color-alice-blue;
}

.advantages__item:hover .advantages__item-wrapper h3 {
    color: $color-alice-blue;
}

.advantages__item-wrapper svg {
    color: $deep-sky-blue;
}

.advantages__item:hover .advantages__item-wrapper svg {
    color: $color-alice-blue;
}

.advantages__item-wrapper h3 {
    font-family: $roboto;
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    color: $deep-sky-blue;
}

.advantages__item-wrapper p {
    font-family: $mono;
    font-weight: 400;
    font-size: 16px;
    line-height: 135%;
    color: $black-pearl;
}