.form {
  background: linear-gradient(139deg, #04182780 0 40%, #00b3ff 40% 100%);
  margin: 0 auto;
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #00b3ff;

  @media (max-width: $vp-1023) {
    background: $deep-sky-blue;
  }
}

.form__container {
  max-width: 1320px;
  flex: 1;
}

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0 50px;
  gap: 120px;
  padding-top: 170px;
  padding-bottom: 245px;

  @media (max-width: $vp-1023) {
    display: block;
    padding-top: 50px;
    padding-bottom: 125px;
  }

  @media (max-width: $vp-767) {
    padding: 0 25px;
    padding-bottom: 90px;
  }
}

.form__image-wrapper {
  @media (max-width: $vp-1023) {
    display: none;
  }
}

.form__form-wrapper h3 {
  font-family: $roboto;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
}

.form__form-wrapper p {
  font-family: $mono;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $color-default-white;
}

.form__checkbox label {
  display: flex;
  font-family: $mono;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;
}

.form__checkbox input {
  cursor: pointer;
}

.form__checkbox label {
  position: relative;
  display: block;
  padding-left: 26px;
}

.form__checkbox input::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  border: 1px solid $color-default-white;
  border-radius: 2px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__checkbox input:checked::after {
  content: "";
  position: absolute;
  background-image: url("../../../img/svg/check.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 15px;
  height: 15px;
  border: 1px solid $color-default-white;
  border-radius: 2px;
  left: 0;
}

.form__checkbox input {
  appearance: none;
}

.form__input-container {
  max-width: 580px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "username telephone"
    "textarea textarea";
  gap: 30px;
  margin-bottom: 35px;

  @media (max-width: $vp-1023) {
    max-width: 100%;
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 1fr;
    grid-template-areas:
      "username"
      "telephone"
      "textarea";
  }
}

.form__label--name {
  grid-area: username;
}

.form__label--tel {
  grid-area: telephone;
}

.form__label--text {
  grid-area: textarea;
}

.form__input-container input {
  border: none;
  background: $form-gradient;
  border-radius: 3px;
  padding: 15px;
  width: 100%;
  // min-width: 275px;
  margin-bottom: 20px;
  color: $color-default-white;
  // grid-column: 1 / 2;
}

.form__input-container textarea {
  background: $form-gradient;
  border-radius: 3px;
  padding: 25px 15px;
  width: 100%;
  border: none;
  color: $color-default-white;
}

.form__input-container textarea::placeholder {
  font-family: $mono;
  color: $color-default-white;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  cursor: pointer;
}

.form__form-wrapper input::placeholder {
  font-family: $mono;
  color: $color-default-white;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  cursor: pointer;
}

.form__input-container textarea:hover {
  background-color: $form-background;
  opacity: 0.8;
}

.form__input-container textarea:focus {
  background-color: $form-background;
  opacity: 0.8;
}

.form__input-container textarea:focus {
  background-color: $form-background;
  opacity: 0.6;
}

.form__input-container input:hover {
  background-color: $form-background;
  opacity: 0.8;
}

.form__input-container input:focus {
  background-color: $form-background;
  opacity: 0.8;
}

.form__input-container input:focus {
  background-color: $form-background;
  opacity: 0.6;
}

.form__submit-container {
  display: grid;
  grid-template-columns: 265px auto;
  gap: 50px;
}

.form__button {
  background: $black-pearl;
  border-radius: 3px;
  padding: 15px 25px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-family: $roboto;
  color: $color-default-white;
  border: none;
  cursor: pointer;
}

.form__button:hover {
  opacity: 0.8;
}

.form__button:focus {
  opacity: 0.8;
}

.form__button:active {
  opacity: 0.6;
}
