@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto-Condensed";
  font-display: swap;
  src: url("../fonts/Roboto-Condensed-Bold.woff2") format("woff2"), url("../fonts/Roboto-Condensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto-Condensed";
  font-display: swap;
  src: url("../fonts/Roboto-Condensed-Regular.woff2") format("woff2"), url("../fonts/Roboto-Condensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: centers;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  width: 100%;
  position: fixed;
  z-index: 10;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    position: relative;
    z-index: 10;
  }
}

.header__wrapper {
  max-width: 1320px;
  flex: 1;
}

.header__container {
  display: flex;
  flex-grow: 1;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  justify-content: space-between;
  padding: 0 50px;
  padding-bottom: 35px;
}
@media (max-width: 767px) {
  .header__container {
    padding: 0 25px;
    padding-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .header__logo-link img {
    width: 35px;
    height: 35px;
  }
}
@media (max-width: 320px) {
  .header__logo-link img {
    width: 35px;
    height: 35px;
  }
}

.header__logo-link {
  margin-top: 30px;
}
@media (max-width: 767px) {
  .header__logo-link {
    margin-top: 15px;
    margin-left: 0;
  }
}

.header__logo-link:hover {
  opacity: 0.8;
}

.header__logo-link:focus {
  opacity: 0.8;
}

.header__logo-link:active {
  opacity: 0.6;
}

.header__contacts-link span {
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .header__contacts-link span {
    display: none;
  }
}

.header__order-call span {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 10px 17px;
  padding-bottom: 12px;
}

.header__order-call {
  margin-top: 35px;
  display: flex;
  align-items: center;
  color: #f2f5f6;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  height: fit-content;
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .header__order-call {
    display: none;
  }
}

.header__order-call:hover {
  opacity: 0.8;
}

.header__order-call:focus {
  opacity: 0.8;
}

.header__order-call:active {
  opacity: 0.6;
}

.header__contacts-link {
  height: fit-content;
  padding: 10px 9px;
  display: flex;
  align-items: center;
}

.header__contacts-link:hover {
  opacity: 0.8;
}

.header__contacts-link:focus {
  opacity: 0.8;
}

.header__contacts-link:active {
  opacity: 0.6;
}

.header__contacts-link svg {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header__contacts-link svg {
    margin-right: 0;
  }
}

.header__contacts-container {
  display: grid;
  grid-template-columns: 225px 230px;
  gap: 30px;
  margin-top: 35px;
}
@media (max-width: 1023px) {
  .header__contacts-container {
    grid-template-columns: 40px 40px;
    gap: 20px;
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .header__contacts-container {
    margin-top: 10px;
  }
}

.features {
  background-color: #041827;
  width: 100%;
  display: flex;
  justify-content: center;
  flex: 1;
}

.features__container {
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 70px;
  padding-bottom: 140px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 767px) {
  .features__container {
    padding: 0 25px;
    padding-top: 50px;
    padding-bottom: 86px;
  }
}

.features__title {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin-bottom: 55px;
  margin-top: 0;
}
@media (max-width: 767px) {
  .features__title {
    display: none;
  }
}

.features__title---mobile {
  display: none;
}
@media (max-width: 767px) {
  .features__title---mobile {
    font-family: "Roboto-Condensed", "Arial", sans-serif;
    font-weight: 400;
    display: block;
    font-size: 28px;
    line-height: 39px;
    color: #ffffff;
    margin-top: 0;
    margin-bottom: 40px;
  }
}

.features__list {
  list-style: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 85px;
  margin: 0;
}
@media (max-width: 1023px) {
  .features__list {
    grid-template-columns: 1fr;
    gap: 30px;
    width: 100%;
  }
}

.features__link {
  position: relative;
  display: block;
}

.features__link:hover {
  opacity: 0.8;
}

.features__link:focus {
  opacity: 0.8;
}

.features__link:active {
  opacity: 0.6;
}

.features__link::after {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../../img/svg/arrow.svg");
  background-color: #00b2ff;
  color: #ffffff;
  background-position: center;
  background-repeat: no-repeat;
  bottom: 0;
  right: 0;
}
@media (max-width: 767px) {
  .features__link::after {
    width: 40px;
    height: 40px;
  }
}

.features__link img {
  display: block;
}
@media (max-width: 1023px) {
  .features__link img {
    object-fit: cover;
    width: 100%;
  }
}

.features__link span {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  position: absolute;
  width: 45%;
  top: 7%;
  left: 12%;
}
@media (max-width: 1023px) {
  .features__link span {
    width: 24%;
  }
}
@media (max-width: 767px) {
  .features__link span {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    width: 60%;
  }
}

.accordion {
  margin: 0 auto 0 auto;
  display: grid;
  gap: 30px;
  grid-template-columns: auto auto;
}
@media (max-width: 767px) {
  .accordion {
    display: block;
  }
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .accordion__element:last-child {
    margin-bottom: 0;
  }
}
.accordion__element {
  padding: 10px;
}
@media (max-width: 767px) {
  .accordion__element {
    margin-bottom: 34px;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    position: relative;
  }
}
.accordion__button {
  display: none;
}
@media (max-width: 767px) {
  .accordion__button {
    width: 100%;
    font-family: "Roboto-Condensed", "Arial", sans-serif;
    padding: 10px;
    font-size: 15px;
    line-height: 16px;
    /* or 107% */
    color: #ffffff;
    display: flex;
    align-items: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    background-color: #041827;
    border: none;
    cursor: pointer;
    position: relative;
  }
}
.accordion__wrapper {
  color: #ffffff;
}

.accordion__button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 15px;
  height: 1px;
  background-color: #f2f5f6;
  transform: rotate(-270deg);
}

.accordion__button::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 15px;
  height: 1px;
  background-color: #f2f5f6;
}

.accordion__element.is-active > .accordion__button::after {
  transform: rotate(-180deg);
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 15px;
  height: 1px;
  background-color: #f2f5f6;
}

.accordion__content {
  display: flex;
}

.accordion__wrapper h3 {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .accordion__wrapper h3 {
    display: none;
  }
}

.accordion__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  /* or 22px */
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .accordion__link {
    font-size: 14px;
  }
}

.accordion__link:hover {
  opacity: 0.8;
}

.accordion__link:focus {
  opacity: 0.8;
}

.accordion__link:active {
  opacity: 0.6;
}

.accordion__address {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  /* or 22px */
  color: rgba(255, 255, 255, 0.7);
  font-style: normal;
}

.accordion__list {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  display: grid;
  column-gap: 9%;
  row-gap: 10px;
}
@media (max-width: 1023px) {
  .accordion__list {
    display: block;
  }
}

.accordion__list .accordion__item {
  list-style: none;
}
.accordion__list .accordion__item:nth-child(1) {
  grid-column: 1/2;
}
.accordion__list .accordion__item:nth-child(2) {
  grid-column: 1/2;
}
.accordion__list .accordion__item:nth-child(3) {
  grid-column: 1/2;
}
.accordion__list .accordion__item:nth-child(4) {
  grid-column: 1/2;
}
.accordion__list .accordion__item:nth-child(5) {
  grid-column: 2/2;
  grid-row: 1/4;
}
.accordion__list .accordion__item:nth-child(6) {
  grid-column: 2/2;
  grid-row: 2/4;
}
.accordion__list .accordion__item:nth-child(7) {
  grid-column: 2/2;
  grid-row: 3/4;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.footer {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #041827;
  overflow: hidden;
}

.footer__block {
  max-width: 1320px;
  flex: 1;
}

@media (max-width: 1023px) {
  .footer__logos {
    display: flex;
    margin-right: -5px;
    justify-content: space-between;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .footer__logos {
    flex-direction: column;
    align-items: center;
    margin-bottom: 25px;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 35px;
  }
}

.footer__logo-link:hover {
  opacity: 0.8;
}

.footer__logo-link:focus {
  opacity: 0.8;
}

.footer__logo-link:active {
  opacity: 0.6;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  padding-top: 50px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    padding: 0 25px;
    padding-top: 50px;
    display: block;
  }
